import React from "react";
import buttonStyle from "./button.module.scss"
import download from './photo_2021-11-30_13-53-12.jpg'

type ButtonComponentsPropsType = {
    buttonType?: "button" | "reset" | "submit" | undefined
    title: string
    link?: string
    download?: string
}

export function ButtonComponent(props: ButtonComponentsPropsType) {

    return (
        <button style={{zIndex: 1}} type={props.buttonType} className={buttonStyle.wrapButton}>
            {props.download ?
                <a download={download} href={"#"} className={buttonStyle.button}>{props.title}</a> :
                <a href={props.link} className={buttonStyle.button}>{props.title}</a>}
        </button>
    )
}


