import React from "react";
import style from "./Contact.module.scss"
import {TitleComponents} from "../common/components/titleComponets/TitleComponents";
import commonStyle from "../common/commonStyle/sass/common.module.scss"
import {ButtonComponent} from "../common/components/buttonComponent/ButtonComponent";
//@ts-ignore
import Fade from 'react-reveal/Fade';


export function Contact() {

    return (
        <div className={commonStyle.commonBlock}>
            <Fade>
                <div className={commonStyle.commonContainer} style={{textAlign: "center"}}>
                    <TitleComponents title={"Contact"}/>
                    <div className={style.innerBlock}>
                        <form className={style.forma} action="">
                            <input placeholder={"Name"} type="text"/>
                            <input placeholder={"Email"} type="email"/>
                            <textarea placeholder={"Message"}/>
                            <ButtonComponent title={"SEND MESSAGE"}/>
                        </form>
                        <div className={style.blockMap}>
                            <iframe
                                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d150505.20100923395!2d27.453286768317984!3d53.88476083077986!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x46dbcfd35b1e6ad3%3A0xb61b853ddb570d9!2z0JzQuNC90YHQug!5e0!3m2!1sru!2sby!4v1636710848058!5m2!1sru!2sby"
                                loading="lazy"/>
                        </div>
                    </div>
                </div>
            </Fade>
        </div>
    )
}

export default Contact;
